import Vue from 'vue'

const state = {
  _artist: null,
  _hotSongs: null
}

const getters = {
  artist(s) {
    if(!s._artist){
      return false
    }
    let {alias, briefDesc, id, name, picUrl} = s._artist, ar
    ar = {
      id,
      picUrl,
      name,
      briefDesc,
      alias: alias[0]
    }

    return ar
  },
  hotSongs(s){
    if(!s._hotSongs){
      return false
    }
    let _hs = s._hotSongs, hs = []
    _hs.forEach(({id, name, al, ar, alia}) => {
      let _ar = []
      ar.forEach(({name})=>{
        _ar.push(name)
      })
      hs.push({
        id,
        name,
        album: al.name,
        alias: alia[0],
        artist: _ar.join('/')
      })
    })
    return hs
  }
}

const mutations = {
  setArtist(s, { artist, hotSongs }) {
    s._artist = artist
    s._hotSongs = hotSongs
  }
}

const actions = {
  getArtist({ commit, getters }, { id }) {
    if (!(!getters.artist || (id && getters.artist.id != id))) {
      return false
    }
    Vue.http.get(process.env.SERVER + '/artists?id=' + id).then(({ body: { artist, hotSongs } }) => {
      commit('setArtist', { artist, hotSongs })
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}


