import Vue from 'vue'

const state = {
  _user: null,
  _playlist: null
}

const getters = {
  user(s) {
    if (!s._user) {
      return false
    }
    let { listenSongs, avatarUrl, backgroundUrl, followeds, nickname, gender } = s._user, u
    u = {
      listenSongs,
      avatarUrl,
      backgroundUrl,
      followeds,
      nickname,
      gender
    }
    return u
  },
  playlist(s) {
    if (!s._playlist) {
      return false
    }
    let _p = s._playlist, p = []

    _p.forEach(({ id, name, playCount, trackCount, coverImgUrl }) => {
      p.push({
        id, name, playCount, trackCount, coverImgUrl
      })
    })
    return p
  }
}

const mutations = {
  setDetail(s, { profile }) {
    s._user = profile
  },
  setPlaylist(s, { playlist }) {
    s._playlist = playlist
  }
}

const actions = {
  getUser({ commit,getters }, { id }) {
    if(!getters.user || (id && getters.user.id != id)){
      let p1 = Vue.http.get(process.env.SERVER + '/user/detail?uid=' + id),
        p2 = Vue.http.get(process.env.SERVER + '/user/playlist?uid=' + id)
      
      Promise.all([p1, p2]).then(res => {
        res[0].body.profile.listenSongs = res[0].body.listenSongs
        commit('setDetail', { profile: res[0].body.profile })
        commit('setPlaylist', { playlist: res[1].body.playlist })
      })
    }
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}


