import Vue from 'vue'
import util from '../../assets/js/util'

const state = {
  _album: null,
  _songs: null,
  _comments: null,
  _hotComments: null
}

const getters = {
  album(s) {
    if (!s._album) {
      return false
    }
    let { id, picUrl, artist, description, name, status, publishTime } = s._album, d
    let pt = new Date(publishTime)
    d = {
      id,
      picUrl,
      artist: {
        id: artist.id,
        name: artist.name
      },
      description,
      name,
      status,
      publishTime: pt.getFullYear() + '-' + util.zFill(pt.getMonth() + 1) + '-' + util.zFill(pt.getDate())
    }
    return d
  },
  songs(s) {
    if (!s._songs) {
      return false
    }
    let _s = s._songs, ss = []
    _s.forEach(({ id, name, al, ar, alia }) => {
      ss.push({
        id,
        name,
        album: al.name,
        alias: alia[0],
        artist: ar[0].name
      })
    })
    return ss
  },
  comments(s) {
    return s._comments
  },
  hotComments(s) {
    return s._hotComments
  }
}

const mutations = {
  setAlbum(s, { album, songs, comments, hotComments }) {
    comments.forEach(c => {
      let d = new Date(c.time)
      c.timeString = d.getFullYear() + '年' + (d.getMonth() + 1) + '月' + d.getDate() + '日'
      c.translatedMessage = util.replaceEmoji(c.content)

      if (c.beReplied.length > 0) {
        let rep = c.beReplied[0]
        c.translatedBeReplied = {
          user: {
            id: rep.user.userId,
            nickname: rep.user.nickname
          },
          content: '@' + rep.user.nickname + '：' + util.replaceEmoji(rep.content)
        }
      }
    })

    hotComments.forEach(c => {
      let d = new Date(c.time)
      c.timeString = d.getFullYear() + '年' + (d.getMonth() + 1) + '月' + d.getDate() + '日'
      c.translatedMessage = util.replaceEmoji(c.content)

      if (c.beReplied.length > 0) {
        let rep = c.beReplied[0]
        c.translatedBeReplied = {
          user: {
            id: rep.user.userId,
            nickname: rep.user.nickname
          },
          content: '@' + rep.user.nickname + '：' + util.replaceEmoji(rep.content)
        }
      }
    })

    s._album = album
    s._songs = songs
    s._comments = comments
    s._hotComments = hotComments
  }
}

const actions = {
  getAlbum({ commit, getters }, { id }) {
    if (!(!getters.album || (id && getters.album.id != id))) {
      return false
    }
    let p1 = Vue.http.get(process.env.SERVER + '/album?id=' + id),
      p2 = Vue.http.get(process.env.SERVER + '/comment/album?id=' + id)

    Promise.all([p1, p2]).then(res => {
      commit('setAlbum', {
        album: res[0].body.album,
        songs: res[0].body.songs,
        comments: res[1].body.comments,
        hotComments: res[1].body.hotComments
      })
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}


