import Vue from 'vue'
import qs from 'querystring'

const state = {
  _toplist: null,
  _banner: null
}

const getters = {
  toplist(s) {
    if(!s._toplist){
      return false
    }
    let { tracks } = s._toplist, tl = []

    if (tracks) {
      tracks.forEach(t => {
        let art = []
        t.artists.forEach(a => {
          art.push(a.name)
        })
        tl.push({
          id: t.id,
          name: t.name,
          artists: art.join('/'),
          album: t.album.name
        })
      })
    }
    return tl
  }, 
  banner(s) {
    if(!s._toplist){
      return false
    }
    // return s._toplist.coverImgUrl
    if (s._toplist.hasOwnProperty('tracks')) {
      let t = s._toplist.tracks[0]
      return {
        sid: t.id,
        picUrl: t.album.picUrl
      }
    }
    return ''
  }
}

const mutations = {
  setToplist(s, { toplist }) {
    s._toplist = toplist
  }
}

const actions = {
  getToplist({ commit }, payload = {}) {
    if (!payload.idx) {
      return false
    }
    Vue.http.get(process.env.SERVER + '/top/list?' + qs.stringify(payload)).then(({ body: { result } }) => {
      commit('setToplist', { toplist: result })
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}


