// 转场类(class)
const SLIDE_LEFT = 'slide-left',
  SLIDE_RIGHT = 'slide-right'

export default function construct(router /* Router类的实例 */) {
  let proto = router.__proto__,
    fnsArray = {
      [SLIDE_LEFT]: ['push', 'replace', 'forward'],
      [SLIDE_RIGHT]: ['back']
    },
    direction

  // 缓存转场类(class)
  proto.transfer = ''

  // push, replace, forward, back
  for (direction in fnsArray) {
    let fns = fnsArray[direction]

    for (let i = 0, l = fns.length; i < l; i++) {
      let fn = fns[i], _fn = '_' + fn
      // 缓存原函数
      proto[_fn] = proto[fn]
      // 增强函数
      proto[fn] = (function (DIR) {
        return function () {
          // 设置转场动画
          proto.transfer = DIR
          // 用原函数执行转场
          proto[_fn].apply(router, arguments)
        }
      })(direction)
    }
  }

  // go
  proto._go = proto.go
  proto.go = function (n) {
    proto.transfer = n >= 0 ? SLIDE_LEFT : SLIDE_RIGHT
    proto._go.apply(router, arguments)
  }


  /**
   * 对于一些基于store数据来渲染的页面，在应用刚启动时store是没有数据的
   * 在页面转场进入时(完全转场结束之前)会执行与新页面相关的数据请求操作和渲染
   * 因为转场期间并行数据请求和页面渲染时会造成转场的卡顿闪烁
   * 所以在beforeEach先拦截转场进入，预先准备好新页面的数据请求和页面渲染
   * 待页面渲染结束后再执行转场，等待时间默认为100ms
   */
  router.beforeEach(function(to, from, next){
    let preload = to.meta.preload
    if(!preload){
      console.log('next')
      next()
    }else{
      console.log('preload')
      for(let i = 0, l = preload.dispatches.length; i < l; i++){
        router.app.$store.dispatch(preload.dispatches[i], to.query)
      }
      setTimeout(() => {
        next()
      }, preload.timeout || 100);
    }
  })

  return router
}


