import Vue from 'vue'
import util from '../../assets/js/util'

const state = {
  _playlist: null
}

const getters = {
  playlist(s) {
    return s._playlist
  }
}

const mutations = {
  setPlaylist(s, { playlist, comments }) {
    comments.forEach(c => {
      let d = new Date(c.time)
      c.timeString = d.getFullYear() + '年' + (d.getMonth() + 1) + '月' + d.getDate() + '日'
      c.translatedMessage = util.replaceEmoji(c.content)

      if (c.beReplied.length > 0) {
        let rep = c.beReplied[0]
        c.translatedBeReplied = {
          user: {
            id: rep.user.userId,
            nickname: rep.user.nickname
          },
          content: '@' + rep.user.nickname + '：' + util.replaceEmoji(rep.content)
        }
      }
    })
    playlist.comments = comments
    s._playlist = playlist
  }
}

const actions = {
  getPlaylist({ commit, getters }, { id }) {
    if (!(!getters.playlist || (id && getters.playlist.id != id))) {
      return false
    }
    let p1 = Vue.http.get(process.env.SERVER + '/playlist/detail?id=' + id),
      p2 = Vue.http.get(process.env.SERVER + '/comment/playlist?id=' + id)

    Promise.all([p1, p2]).then(res => {
      commit('setPlaylist', { playlist: res[0].body.playlist, comments: res[1].body.comments })
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}


