import Vue from 'vue'
import emoji from '../json/emoji.json'


// video, audio 使用的简单的时间格式化函数
// 只适用于数字转时分秒格式

function timeTransform(time) {
  if (!isNaN(time = parseInt(time))) {
    return Math.floor(time / 60) + ':' + zFill(time % 60)
    // return zFill(Math.floor(time / 60)) + ':' + zFill(time % 60)
  }
  return '0:00'
}

function zFill(time) {
  if (time < 10) {
    return "0" + time
  }
  return "" + time
}

let toString = Object.prototype.toString,
  types = {},
  typesArray = ['Number', 'String', 'Boolean', 'Null', 'Undefined', 'Array', 'Object']

typesArray.forEach(function (val) {
  types['[object ' + val + ']'] = val.toLowerCase()
})

function type(val) {
  return types[toString.call(val)]
}


function createService(name, vnode) {
  let wrapper = document.createElement('div'),
    id = 'v-' + Date.now().toString(32)

  wrapper.id = id

  if (document.body.appendChild(wrapper)) {
    if (Vue.prototype[name]) {
      console.error(`${name}属性或函数已存在`)
      return false
    } else {
      Vue.prototype[name] = new Vue(vnode).$mount('#' + id)
      return true
    }
  } else {
    return false
  }
}

function noop() { }

// 兼容全屏方法
function fullscreen(el) {
  if (!el.fullscreen || typeof el.fullscreen !== 'function') {
    el.fullscreen = el.enterFullScreen || el.webkitEnterFullScreen || el.mozEnterFullScreen || el.requestFullscreen || el.webkitRequestFullscreen || el.msRequestFullscreen || el.mozRequestFullScreen || noop
  }
  el.fullscreen()
}

const deviceInfo = (function device() {
  return {
    width: document.documentElement.clientWidth || document.body.clientWidth,
    height: document.documentElement.clientHeight || document.body.clientHeight
  }
})()


const topicReg = /\#.+\#/g,
  emojiReg = /(\[[a-zA-Z0-9\u4e00-\u9fa5]+\])/g,
  picsIdReg = /==\/\d*\.jpg$/g,
  picsIdTrimReg = /(==\/)|(\.jpg)/g

function replaceTopic(str, replacement) {
  return str ? str.replace(topicReg, replacement) : ''
}

function replaceEmoji(str) {
  return str ? str.replace(emojiReg, w => {
    return `<img class="emoji" src="${emoji[w]}"/>`
  }) : ''
}

export default {
  timeTransform,
  zFill,
  type,
  createService,
  noop,
  fullscreen,
  deviceInfo,
  replaceTopic,
  replaceEmoji
}


