import hello from '../components/HelloWorld.vue'

import home from '../pages/home/home.vue'
import search from '../pages/search/search.vue'
import musicPlayer from '../pages/musicPlayer/interface.vue'
import playlist from '../pages/playlist.vue'
import album from '../pages/album.vue'
import artist from '../pages/artist.vue'
import user from '../pages/user.vue'

export default [
  {
    path: '/',
    name: 'home',
    // component: hello
    // component: resolve => require(['../pages/home/home.vue'], resolve)
    component: home
  },
  {
    path: '/search',
    name: 'search',
    // component: resolve => require(['../pages/search/search.vue'], resolve)
    component: search
  },
  {
    path: '/musicPlayer',
    name: "musicPlayer",
    // component: resolve => require(['../pages/musicPlayer/interface.vue'], resolve)
    component: musicPlayer,
    meta: {
      preload: {
        dispatches: ['musicPlayer/getSongDetail'],
        timeout: 200
      }
    }
  },
  {
    path: '/playlist',
    name: 'playlist',
    // component: resolve => require(['../pages/playlist.vue'], resolve)
    component: playlist,
    meta: {
      preload: {
        dispatches: ['playlist/getPlaylist'],
        timeout: 150
      }
    }
  },
  {
    path: '/album',
    name: 'album',
    // component: resolve => require(['../pages/album.vue'], resolve)
    component: album,
    meta: {
      preload: {
        dispatches: ['album/getAlbum']
      }
    }
  },
  {
    path: '/artist',
    name: 'artist',
    // component: resolve => require(['../pages/artist.vue'], resolve)
    component: artist,
    meta: {
      preload: {
        dispatches: ['artist/getArtist']
      }
    }
  },
  {
    path: '/user',
    name: 'user',
    // component: resolve => require(['../pages/user.vue'], resolve)
    component: user,
    meta: {
      preload: {
        dispatches: ['user/getUser'],
        timeout: 150
      }
    }
  }
]


