<template>
  <div id="music-player-comment" class="comment shadow-3d-reverse" :class="{active}" :style="contentHeight" @click="toggle($event)" ref="comment">
    <h4 class="comment-label">评论
      <span class="count">{{currentPlays.total}}</span>
    </h4>
    <div class="comment-content">
      <list>
        <list-item v-for="c in currentPlays.comments" :key="c.commentId">
          <router-link class="avatar" tag="div" :to="{name: 'user', query: {id: c.user.userId}}">
            <img :src="c.user.avatarUrl" data-toggle="router-link">
          </router-link>
          <list-item-holder>
            <list-item-inner>
              <div class="primary-title">
                <router-link tag="span"  :to="{name: 'user', query: {id: c.user.userId}}" data-toggle="router-link">
                   {{c.user.nickname}}
                </router-link>
                <small class="liked">
                  {{c.likedCount}}&nbsp;&nbsp;
                  <icon ligature="thumb_up"></icon>
                </small>
              </div>
              <div class="third-title">{{c.timeString}}</div>

              <div v-if="!c.translatedBeReplied" class="message" v-html="c.translatedMessage"></div>
              <div class="message" v-else>
                回复
                <router-link class="reply-user-nickname" data-toggle="router-link" :to="{name: 'user', query: {id: c.translatedBeReplied.user.id}}">
                  @{{c.translatedBeReplied.user.nickname}}
                </router-link>：
                <span v-html="c.translatedMessage"></span>
                <div class="message reply" v-html="c.translatedBeReplied && c.translatedBeReplied.content"></div>
              </div>
            </list-item-inner>
          </list-item-holder>
        </list-item>
      </list>
    </div>
  </div>

  
</template>

<script>
import { mapGetters } from "vuex";
import util from "../../assets/js/util.js";
import pipe from "./pipe";

export default {
  data() {
    return {
      active: false,
      tmp: {
        top: 0
      }
    };
  },
  computed: {
    ...mapGetters("musicPlayer", ["currentPlays"]),
    contentHeight() {
      return {
        height: util.deviceInfo.height - 54 + "px"
      };
    }
  },
  methods: {
    toggle(e) {
      e.preventDefault();
      if (e.target.dataset.toggle) {
        return false;
      }
      let el = this.$refs.comment,
        rect = el.getBoundingClientRect(),
        style = el.style;
      if (!this.active) {
        style.position = "fixed";
        style.top = rect.top + "px";

        this.tmp.top = rect.top;

        setTimeout(
          self => {
            self.active = true;
            // el.classList.add('active')
            style.top = "54px";
            // el.scrollIntoView()
            pipe.$emit("toggleMiniUI", { active: true });
          },
          0,
          this
        );
      } else {
        this.active = false;
        // el.classList.remove('active')
        style.top = this.tmp.top + "px";
        pipe.$emit("toggleMiniUI", { active: false });

        setTimeout(() => {
          style.position = "";
          style.top = "";
        }, 400);
      }
    }
  }
};
</script>


