<template>
  <div class="tip text-center" v-html="str" :style="{opacity}"></div>
</template>

<script>
  export default {
    data() {
      return {
        str: "",
        opacity: 0,
        timer: null
      }
    },
    methods: {
      show(str /* string or html */) {
        if (str) {
          this.str = str;
          this.opacity = 1;
          clearTimeout(this.timer)
          this.timer = setTimeout(self => {
            self.hide()
          }, 2000, this);
        }
      },
      hide() {
        this.opacity = 0;
      }
    }
  }
</script>


