<template>
  <page class="home">
    <toolbar fixed class="shadow-3d">
      <router-link to="search" class="btn-icon">
        <icon ligature="search" style="vertical-align: middle;"></icon>
      </router-link>
      <toolbar-inner>
        <tabs-navigation @tabs:change="viewChange">
          <tab-label v-for="(t,i) in tabs" :key="i" :index="i">
            {{t.zh}}
          </tab-label>
        </tabs-navigation>
      </toolbar-inner>
      <router-link v-if="currentPlays" to="musicPlayer" class="btn-icon">
        <spectrum :class="{playing}"></spectrum>
      </router-link>
      <div v-else class="btn-icon" style="width: 40px; height: 20px;"></div>
    </toolbar>
 
    <keep-alive>
      <component :is="currentView" v-cloak></component>
    </keep-alive>
  </page>
</template>

<script>
import { mapGetters } from "vuex";

import find from "./find";
import top from "./top";

export default {
  name: "home",
  data() {
    return {
      currentView: "find",
      tabs: [{ en: "find", zh: "发现" }, { en: "top", zh: "热歌榜" }]
    };
  },
  components: {
    find,
    top
  },
  computed: mapGetters("musicPlayer", ["playing" , 'currentPlays']),
  methods: {
    viewChange(index) {
      setTimeout(
        self => {
          self.currentView = self.tabs[index].en;
        },
        300,
        this
      );
    }
  }
};
</script>


