<script>
export default {
  render(h) {
    let def = this.$slots.default,
      vnode,
      clonedVnode;

    console.log(def[0])
    if (
      def.length === 1 &&
      (vnode = def[0]) &&
      vnode.tag === "img" &&
      vnode.data.attrs && vnode.data.attrs.src
    ) {
      clonedVnode = h(
        "img",
        Object.assign({}, vnode.data, {
          class: { echo: true },
          id: ""
        })
      );
    }

    return h("div", { staticClass: "echo-container" }, [vnode, clonedVnode]);
  },
  updated() {
    console.log('update')
  }
};
</script>



